___CSS_LOADER_EXPORT___.push([module.id, `dialog {
  width: 30%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  font-size: 20px;
  border-radius: 10px;
  background-color: #2c2c3e;
}
.submit {
  width: 100px;
  justify-self: center;
  background-color: #6ab5ff;
  border-radius: 10px;
  font-weight: bolder;
}

dialog:not([open]) {
  display: none;
}
::backdrop {
  backdrop-filter: blur(5px);
}

input {
  padding: 5px;
  border-radius: 100px;
}

textarea {
  border-radius: 10px;
  height: 4rem;
  padding: 5px;
}
select {
  height: 2rem;
  border-radius: 10px;
  padding: 5px;
}

.close {
  justify-self: end;
  border-radius: 100px;
  background-color: #f56565;
}

input,
select,
textarea {
  color: black;
}

.project_form {
  display: grid;

  gap: 10px;
}

option {
  color: black;
}
