___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
  background-color: #252536;
  border-right: 1px solid black;

  grid-column: 1 / 2;
  height: 100vh;
  align-self: stretch;
  grid-auto-rows: auto;
}
.project_heading{
  grid-column: 1 / 2;
  margin-left: 10px;
 
}
.add.project{
  grid-column: 2 / 3;
  width: 4rem;
  font-size: 50px;
  aspect-ratio: 1;
  color: black;
  border-radius: 50%;
  margin-right: 30px;
  height: fit-content;
  justify-self: end;
  align-self: center;
  
}


.projectList{
  grid-column: 1 / 3;
  height: fit-content;
  
}

.project_item {
  color: white;
   border: 1px solid white;
   border-radius: 10px;
   padding-left: 10px;
   margin: 10px;
  
}
.project_item.active{

 
  
   border-left: 10px solid rgb(0, 72, 255);
  
 
}

.project_item:hover {
  
    background-color: darkblue;
}
