___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  font-family: "Times New Roman", Times, serif;
  color: #ffffff;
}

html {
  background-color: #12121b;
}
h1 {
  color: white;
  font-size: 2rem;
}

.header {
  display: flex;
  width: 100%;
  height: 70px;
  padding-left: 50px;
  background-color: wheat;
}

.reset {
  border-radius: 10px;
  color: black;
  height: 30px;
  margin-right: 50px;
  font-size: 25px;
}
button:active {
  background-color: grey;
}

.title {
  color: black;
}

.container {
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 2fr;
}

button {
  background-color: #4fd1c4;
  border: none;

  font-size: 20px;
}

#name {
  width: 100%;
}
